import './App.css';
import Header from "./AllComponents/Header";
import { Todos } from "./AllComponents/Todos";
import { Footer } from "./AllComponents/Footer";
import React, { useState, useEffect } from 'react';
import { AddTodo } from "./AllComponents/AddTodo";


function App() {
  let initTodo;
  if (localStorage.getItem("todos") === null) { // if it is null
    initTodo = [];
  }
  else {
    initTodo = JSON.parse(localStorage.getItem("todos"));
  }

  const onDelete = (todo) => {
    console.log("I am on delete of todo", todo);
    setTodos(todos.filter((e) => {
      return e !== todo;
    }));
    console.log("deleted", todos)
    localStorage.setItem("todos", JSON.stringify(todos));
  }


  const addTodo = (tittle, desc) => {
    console.log("I am adding this in my wishlist", tittle, desc)
    let sno;
    if (todos.length === 0) {
      sno = 0;
    }
    else {
      sno = todos[todos.length - 1].sno + 1;
    }

    
    const myTodo = {
      sno: sno,
      tittle: tittle,
      desc: desc
    }
    setTodos([...todos, myTodo]);
    console.log(myTodo);
  }

  const [todos, setTodos] = useState(initTodo);
  useEffect(() => {
    localStorage.setItem("todos", JSON.stringify(todos));
  }, [todos])

  return (
    <>
      
      <Header title="My Todos List" searchBar={true} /> 
      <AddTodo addTodo={addTodo} />
      <Todos todos={todos} onDelete={onDelete} /> 
      <Footer />
    
    </>
  );
}

export default App;
