import React from 'react'



export const Footer = () => {
    let footerStyle ={
        position: "relative",
        top: "20vh",
        width: "100%",
        border: "5px solid yellow"
        
    }
    return (
        <footer className="bg-dark text-light py-4" style={footerStyle}>
            <p className="text-center">
                Copyright &copy; MyTodosList.com
            </p>
        </footer>
    )
}

