import React from 'react'

export const TodoItem = ({todo, onDelete}) => {
    return (
        <>
        <div>
            <h4>{todo.tittle}</h4>
            <p>{todo.desc}</p>
            {/*
             arrow function is use to only pass the function
            */}
            <button className="btn btn-sm btn-danger" onClick={()=> {onDelete(todo)}} >Delete</button>
        </div>
        <hr/>
        </>
    )
}
